
import React from 'react';

import { View, FormStatus, FormLayout, Gallery, Separator, Tabbar, TabbarItem, Placeholder, Avatar, Counter, Card, CardGrid, platform, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';


//иконки
import Icon28AirplayVideoOutline from '@vkontakte/icons/dist/28/airplay_video_outline'; //обучение
import Icon28LocationOutline from '@vkontakte/icons/dist/28/location_outline'; //такси
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; //covid
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline'; //новости
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline'; //организации
import Icon28MenuOutline from '@vkontakte/icons/dist/28/menu_outline'; //ещё


const osname = platform();

const Covid = props => (
    <Panel id={props.id}>

        <PanelHeader
        left={<PanelHeaderButton>
        {osname === IOS ? <Icon28BugOutline/> : <Icon28BugOutline/>}
    </PanelHeaderButton>}
        >
         COVID-19
        </PanelHeader>
        <Group>


        <Gallery
     slideWidth="100%"
     align="center"
     style={{ height: 230 }}
   >
       <Card size="l">
 <Placeholder
 icon={<Avatar size={45} mode="image" src="https://sun1-23.userapi.com/YZNJNpbIeEVuBpEPdkrbKMpNGnmhTrxLPLovnw/rSKlCwHEF-0.jpg" />}
 header="Выздоровело: 11"
>
 Количество выписанных
 <br/>
 за
 <br/>
 весь период.
</Placeholder>
<div style={{ width: 96, height: 150 }} />
</Card>
     <Card size="l">
<Placeholder
 icon={<Avatar size={45} mode="image" src="https://sun1-23.userapi.com/Y1jzNlA29PrMKTfVgRhn1n2LFsQIgdf500yLZA/8NDwJYdcJAI.jpg" />} 
 header="Заражено: 40"
>
 Количество людей,
 <br/>
 которые
 <br/>
 находятся на лечении.
</Placeholder>
<div style={{ width: 96, height: 150 }} />
</Card>
     <Card size="l">
<Placeholder
 icon={<Avatar size={45} mode="image" src="https://sun1-30.userapi.com/pME0o0gSVhF5cA2AmBKOicUPW0yA_Imz794WXQ/S8J8BT4OjKs.jpg" />}
 header="Умерло: 0"
>
 Количество умерших
 <br/>
 людей за
  <br/>
  весь период.
</Placeholder>
<div style={{ width: 96, height: 150 }} />
</Card>
   </Gallery>

   <br/>
   <FormLayout>
       <FormStatus header="Подсказка" mode="warning">
       Чтобы узнать больше информации, сделайте свайп по табличке в сторону.
       </FormStatus>
   </FormLayout>
      <br/>
      <br/>
      <CardGrid>
          <Card size="l" mode="shadow">
          <Cell before={<Avatar size={28} mode="image" src="https://sun1-28.userapi.com/dV8-DvAWfasJ-kyX129bGR0_t_I1AQ_BvDctoQ/pAamsaQq9ZE.jpg" />} indicator={<Counter mode="primary">10:31</Counter>}>Когда обновилась:</Cell>
            <div style={{ height: 1 }} />
          </Card>
        </CardGrid>

        

    </Group>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
            <Tabbar>
              <Separator wide />
                      <TabbarItem text="Организ." onClick={props.go} data-to="organizations"><Icon28HomeOutline />
                      </TabbarItem>
                      <TabbarItem text="COVID" onClick={props.go} data-to="covid"><Icon28BugOutline />
                      </TabbarItem>
                      <TabbarItem text="Новости" onClick={props.go} data-to="home"><Icon28NewsfeedOutline />
                      </TabbarItem>
                      <TabbarItem text="Обучение" onClick={props.go} data-to="education"><Icon28AirplayVideoOutline />
                      </TabbarItem>
                      <TabbarItem text="Такси" onClick={props.go} data-to="taxi"><Icon28LocationOutline />
                      </TabbarItem>
                      <TabbarItem text="Ещё" onClick={props.go} data-to="still"><Icon28MenuOutline />
                      </TabbarItem>
          </Tabbar>
    </Panel>
);

export default Covid;